header {
  display: block;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
  .wrap {
    position: relative;
    display: block;
    width: 100%;
    height: 300px;
    margin: 0 auto;
    max-width: 980px;
    .logo {
      display: block;
      width: 300px;
      position: absolute;
      left: 10px;
      top: 50%;
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      transform: translateY(-50%);
      img {
        display: block;
        margin: 0;
        width: 100%;
      }
    }
  }
}